import React, {PureComponent} from 'react';
import Request from '../../lib/Request';
import UserInfo from "../../lib/UserInfo";
import history from "../../lib/history";
import {createStandaloneToast } from "@chakra-ui/react"

export interface Props {
  urlParams: any;
}

interface State {
}

class Home extends PureComponent<Props, State> {
  constructor (props) {
    super(props);
    this.state = {
    };
  }
  componentDidMount() {
    if (this.props.urlParams.token)
      this.tryLogin();
    if (this.props.urlParams.error)
      this.setState({loginValid: false})
    if (this.props.urlParams.inviteCode && this.props.urlParams.email) {
      this.sendInviteAccepted();
    }
  }
 

  render() {
    return (
      <div className="container">
        //primo test
        <div className="Button">
          <div className="Hello">{ UserInfo.get("username")}</div>
          <button onClick={this.addUser}>
            Add user!
          </button>
          <button onClick={this.deleteUser}>
            Delete user!
          </button>
          <br/>
          <br/>
          <br/>
        </div>

        //secondo test
        <div className="row">
          <div className="col-xl-4">
            <div className="card card-custom gutter-b card-stretch">
              <div className="card-header border-0 pt-5">
                <div className="card-title">
                  <div className="card-label">
                    <div className="font-weight-bolder">Weekly Sales Stats</div>
                    <div className="font-size-sm text-muted mt-2">890,344 Sales</div>
                  </div>
                </div>
                <div className="card-toolbar">
                  <div className="dropdown dropdown-inline">
                    <a href="#" className="btn btn-clean btn-sm btn-icon" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                      <i className="ki ki-bold-more-hor"></i>
                    </a>
                    <div className="dropdown-menu dropdown-menu-md dropdown-menu-right">
                      <ul className="navi navi-hover py-5">
                        <li className="navi-item">
                          <a href="#" className="navi-link">
                            <span className="navi-icon">
                              <i className="flaticon2-drop"></i>
                            </span>
                            <span className="navi-text">New Group</span>
                          </a>
                        </li>
                        <li className="navi-item">
                          <a href="#" className="navi-link">
                            <span className="navi-icon">
                              <i className="flaticon2-list-3"></i>
                            </span>
                            <span className="navi-text">Contacts</span>
                          </a>
                        </li>
                        <li className="navi-item">
                          <a href="#" className="navi-link">
                            <span className="navi-icon">
                              <i className="flaticon2-rocket-1"></i>
                            </span>
                            <span className="navi-text">Groups</span>
                            <span className="navi-link-badge">
                              <span className="label label-light-primary label-inline font-weight-bold">new</span>
                            </span>
                          </a>
                        </li>
                        <li className="navi-item">
                          <a href="#" className="navi-link">
                            <span className="navi-icon">
                              <i className="flaticon2-bell-2"></i>
                            </span>
                            <span className="navi-text">Calls</span>
                          </a>
                        </li>
                        <li className="navi-item">
                          <a href="#" className="navi-link">
                            <span className="navi-icon">
                              <i className="flaticon2-gear"></i>
                            </span>
                            <span className="navi-text">Settings</span>
                          </a>
                        </li>
                        <li className="navi-separator my-3"></li>
                        <li className="navi-item">
                          <a href="#" className="navi-link">
                            <span className="navi-icon">
                              <i className="flaticon2-magnifier-tool"></i>
                            </span>
                            <span className="navi-text">Help</span>
                          </a>
                        </li>
                        <li className="navi-item">
                          <a href="#" className="navi-link">
                            <span className="navi-icon">
                              <i className="flaticon2-bell-2"></i>
                            </span>
                            <span className="navi-text">Privacy</span>
                            <span className="navi-link-badge">
                              <span className="label label-light-danger label-rounded font-weight-bold">5</span>
                            </span>
                          </a>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
              <div className="card-body d-flex flex-column px-0">
                <div id="kt_tiles_widget_1_chart" data-color="info" className="card-example"></div>
                <div className="flex-grow-1 card-spacer-x">
                  <div className="d-flex align-items-center justify-content-between mb-10">
                    <div className="d-flex align-items-center mr-2">
                      <div className="symbol symbol-50 symbol-light mr-3 flex-shrink-0">
                        <div className="symbol-label">
                          <img src="img/006-plurk.svg" alt="" className="h-50" />
                        </div>
                      </div>
                      <div>
                        <a href="#" className="font-size-h6 text-dark-75 text-hover-primary font-weight-bolder">Top Authors</a>
                        <div className="font-size-sm text-muted font-weight-bold mt-1">Ricky Hunt, Sandra Trepp</div>
                      </div>
                    </div>
                    <div className="label label-light label-inline font-weight-bold text-dark-50 py-4 px-3 font-size-base">+105$</div>
                  </div>
                  <div className="d-flex align-items-center justify-content-between mb-10">
                    <div className="d-flex align-items-center mr-2">
                      <div className="symbol symbol-50 symbol-light mr-3 flex-shrink-0">
                        <div className="symbol-label">
                          <img src="img/015-telegram.svg" alt="" className="h-50" />
                        </div>
                      </div>
                      <div>
                        <a href="#" className="font-size-h6 text-dark-75 text-hover-primary font-weight-bolder">Bestsellers</a>
                        <div className="font-size-sm text-muted font-weight-bold mt-1">Pitstop Email Marketing</div>
                      </div>
                    </div>
                    <div className="label label-light label-inline font-weight-bold text-dark-50 py-4 px-3 font-size-base">+60$</div>
                  </div>
                  <div className="d-flex align-items-center justify-content-between">
                    <div className="d-flex align-items-center mr-2">
                      <div className="symbol symbol-50 symbol-light mr-3 flex-shrink-0">
                        <div className="symbol-label">
                          <img src="img/003-puzzle.svg" alt="" className="h-50" />
                        </div>
                      </div>
                      <div>
                        <a href="#" className="font-size-h6 text-dark-75 text-hover-primary font-weight-bolder">Top Engagement</a>
                        <div className="font-size-sm text-muted font-weight-bold mt-1">KT.com solution provider</div>
                      </div>
                    </div>
                    <div className="label label-light label-inline font-weight-bold text-dark-50 py-4 px-3 font-size-base">+75$</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-xl-5">
            <div className="row">
              <div className="col-xl-6">
                <div className="card card-custom bg-danger gutter-b card-example2">
                  <div className="card-body d-flex flex-column p-0">
                    <div className="flex-grow-1 card-spacer-x pt-6">
                      <div className="text-inverse-danger font-weight-bold">Author Stats</div>
                      <div className="text-inverse-danger font-weight-bolder font-size-h3">3,620</div>
                    </div>
                    <div id="kt_tiles_widget_2_chart" className="card-rounded-bottom card-example3" ></div>
                  </div>
                </div>
                <div className="card card-custom bgi-no-repeat bgi-no-repeat bgi-size-cover gutter-b card-example4">
                  <div className="card-body d-flex flex-column">
                    <a href="#" className="text-dark-75 text-hover-primary font-weight-bolder font-size-h3">Properties</a>
                  </div>
                </div>
              </div>
              <div className="col-xl-6">
                <div className="card card-custom gutter-b card-example2">
                  <div className="card-body d-flex flex-column">
                    <div className="flex-grow-1">
                      <div className="text-dark-50 font-weight-bold">Total Sales</div>
                      <div className="font-weight-bolder font-size-h3">4,9M</div>
                    </div>
                    <div className="progress progress-xs">
                      <div className="progress-bar bg-primary card-example5" ></div>
                    </div>
                  </div>
                </div>
                <div className="card card-custom bg-info gutter-b card-example2">
                  <div className="card-body d-flex flex-column p-0">
                    <div className="flex-grow-1 card-spacer-x pt-6">
                      <div className="text-inverse-info font-weight-bold">Commisions</div>
                      <div className="text-inverse-info font-weight-bolder font-size-h3">$2,005</div>
                    </div>
                    <div id="kt_tiles_widget_5_chart" className="card-rounded-bottom card-example3"></div>
                  </div>
                </div>
              </div>
            </div>
            <div className="card card-custom bgi-no-repeat gutter-b card-example7">
              <div className="card-body d-flex align-items-center">
                <div className="py-2">
                  <h3 className="text-white font-weight-bolder mb-3">30% Off Themes</h3>
                  <p className="text-white font-size-lg">Get your discounted themes of the month
                    <br />No hassle, no worries, no fuss
                    <br />Instant rewards, everyday</p>
                </div>
              </div>
            </div>
          </div>
          <div className="col-xl-3">
            <div className="card card-custom bgi-no-repeat gutter-b card-stretch card-example6">
              <div className="card-body">
                <div className="p-4">
                  <h3 className="text-white font-weight-bolder my-7">Create CRM Reports</h3>
                  <p className="text-muted font-size-lg mb-7">Cause marketing is marketing
                    <br />done by a for-profit business
                    <br />that seeks to increase profits
                    <br />to better society</p>
                  <a href='#' className="btn btn-danger font-weight-bold px-6 py-3">Create Report</a>
                </div>
              </div>
            </div>
          </div>
        </div>

      </div>
    );
  }

  tryLogin() {
    Request.getNewTokenFromRefresh(this.props.urlParams.token, () => history.push("/Home"),() => this.setState({loginValid: false}));
  }

  addUser() {
    Request.api("GET", "/add", null, null, null)
  }

  deleteUser() {
    Request.authenticatedApi("GET", "/api/user/delete", null, null, null)
  }

  sendInviteAccepted() {
    const toast = createStandaloneToast();
    Request.api("POST", "/api/user/invite/accept",
      {email: this.props.urlParams.email, inviteCode: this.props.urlParams.inviteCode},
      () => {
        return toast({
          title: 'Complimenti! ti sei unito alla lega',
          status: 'success',
          duration: 6000,
          isClosable: false,
        });
      }, () => {
        return toast({
          title: 'Ops! Qualcosa  andato storto! Non ti sei unito alla lega.',
          status: 'error',
          duration: 3000,
          isClosable: false,
        });
      })
  }


}

export default Home;
